import React from "react"; 
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import './Header.css';

function Header() {
  return (
    <Navbar expand="lg" className="gentle-navbar">
      <Container fluid>
        {/*   */}
        <Nav className="me-auto align-items-center gentle-left-nav">
          <NavDropdown title="" id="nav-dropdown-sunglass">
            <NavDropdown.Item></NavDropdown.Item>
            <NavDropdown.Item> </NavDropdown.Item>
            <NavDropdown.Item></NavDropdown.Item>
            <NavDropdown.Item></NavDropdown.Item>
            <NavDropdown.Item>2025 </NavDropdown.Item>
            <NavDropdown.Item> </NavDropdown.Item>
          </NavDropdown>
          <NavDropdown title="" id="nav-dropdown-glasses">
            <NavDropdown.Item></NavDropdown.Item>
            <NavDropdown.Item> </NavDropdown.Item>
            <NavDropdown.Item></NavDropdown.Item>
          </NavDropdown>
          <Nav.Link></Nav.Link>
          <Nav.Link></Nav.Link>
          <Nav.Link> </Nav.Link>
        </Nav>

        {/*   */}
        <Navbar.Brand
          className="mx-auto gentle-logo"
        >
          <img src="/img/logo.png" alt="toplogo"></img>
        </Navbar.Brand>

        {/*   */}
        <Nav className="ms-auto align-items-center gentle-right-nav">
          {/*   src     */}
          <Nav.Link>
            <img src="/img/search.svg" alt="search" className="gentle-icon" />
          </Nav.Link>
          <Nav.Link>
            <img src="/img/user.svg" alt="user" className="gentle-icon" />
          </Nav.Link>
          <Nav.Link>
            <img src="/img/bag.svg" alt="bag" className="gentle-icon" />
          </Nav.Link>
        </Nav>
      </Container>
    </Navbar>
  );
}


export default Header;